


























import {Component, Vue} from 'vue-property-decorator';

@Component
export default class HelloWorld extends Vue {
    public listOfData: string[] = [];
    public currentTODO: string = '';

    public onEnter() {
        if (this.currentTODO.trim()) {
            this.listOfData.push(this.currentTODO);
            this.currentTODO = '';
        }

    }

    public mounted() {
        if (localStorage.getItem('Data')) {
            const item = localStorage.getItem('Data');
            if (item) {
                this.listOfData = JSON.parse(item);
            }
        }
    }

    public beforeDestroy() {
        if (this.listOfData && this.listOfData.length > 0) {
            localStorage.setItem('Data', JSON.stringify(this.listOfData));
        }
    }
}

